import React, {Component} from 'react';
import { Button } from 'react-bootstrap';

class Trivia extends Component {
    constructor(){
        super()
        this.state = {
            question: [],
            options: [],
            playerResponse: "",
            questions: [],
            round: 0,
            turn: 0,
            score: 0
          };

        this.handleChange = this.handleChange.bind(this)
        this.handleSubmit = this.handleSubmit.bind(this)
    }

    componentDidMount() {
        //Get random questions
        fetch("https://opentdb.com/api.php?amount=10")
        .then(res => res.json())
        .then(
        (res) => {
          this.setState({
            isLoaded: true,
            questions: res.results
          });
        },
        (error) => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      )
        
      //Set questions
      //this.setState( { question: this.questions[0] } )

      //
    }

    handleChange(event) {
        
    }

    //Handle player answer
    handleSubmit(event) {
        event.preventDefault(); //stop refresh
        this.setState( { playerResponse: event.target.value } )//set response
        
        //Increase score if correct
        if(event.target.value === this.state.question.correct_answer)
        {
          this.setState( { score: this.state.score + 1 } )
        }
        
        //set next question options
        this.setState( { question: this.state.questions[0] } )

        let answer = this.state.question.correct_answer
        let options = [answer].concat(this.state.question.incorrect_answers)
        //shuffle and set options
        this.setState( { options: this.shuffleArray(options) } )

        //Go to next question - increase turn



    }

    // O(n)
    shuffleArray(array) {
      for (let i = array.length - 1; i > 0; i--) {
          const j = Math.floor(Math.random() * (i + 1));
          [array[i], array[j]] = [array[j], array[i]];
      }
    }

    render() {
        const { error, isLoaded} = this.state;
        if (error) {
          return <div>Error: {error.message}</div>;
        } else if (!isLoaded) {
          return <div>Loading...</div>;
        } else {
          return (
            <div id="trivia_container">
              <div id="question">{this.state.question}</div>{this.state.question.difficulty}
              <div className="d-grid gap-2">
                  <Button value={this.state.question.correct_answer} onClick={this.handleSubmit} size="lg">{this.state.question.correct_answer}</Button>
                  <Button value={this.state.question.incorrect_answers[0]} onClick={this.handleSubmit} size="lg">{this.state.question.incorrect_answers[0]}</Button>
                  <Button value={this.state.question.incorrect_answers[1]} onClick={this.handleSubmit} size="lg">{this.state.question.incorrect_answers[1]}</Button>
                  <Button value={this.state.question.incorrect_answers[2]} onClick={this.handleSubmit} size="lg">{this.state.question.incorrect_answers[2]}</Button>
                </div>
            </div>
          ); 
        }
      }
} 

export default Trivia;
  